import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import SignUp from './signUp/signUp';
import SignIn from './SignIn/signIn';
import UserPage from './userPage/userPage';
import Header from './Header/header';
import BottomNav from './BottomNav/bottomNav';
import MealItem from './mealItem/mealItem';
import AddMeal from './AddMeal/addMeal';
import './app.css';

const App = () => (
  <div>
    <Router>
      <Route path="/vzdrizhni/Food-tracker-front" component={Header} />
      <Switch>
        <Route exact path="/vzdrizhni/Food-tracker-front" component={SignUp} />
        <Route exact path="/vzdrizhni/Food-tracker-front/sign_in" component={SignIn} />
        <Route exact path="/vzdrizhni/Food-tracker-front/userpage" component={UserPage} />
        <Route exact path="/vzdrizhni/Food-tracker-front/meal/:id" component={MealItem} />
        <Route exact path="/vzdrizhni/Food-tracker-front/addmeasure" component={AddMeal} />
      </Switch>
      <Route path="/vzdrizhni/Food-tracker-front" component={BottomNav} />
    </Router>
  </div>
);

export default App;
